export default {
  layout: 'none',
  components: {},
  data() {
    return {
      passwordDisplay: false,
      loginLoading: false,
      loginForm: {
        account: '',
        password: ''
      },
      rules: {
        required: value => !!value.trim() || '必填，不可为空',
        mail: value => (!value.includes('@') && !value.includes('.')) || /^\S+@\S+\.\S+?$/.test(value) || '请输入正确的用户名或邮箱'
      },
      errMsg: '',
      redirect: undefined,
      otherQuery: {}
    }
  },
  computed: {
  },
  watch: {
    $route: {
      handler(route) {
        const query = route.query
        if (query) {
          this.redirect = query.redirect
          this.otherQuery = this.getOtherQuery(query)
        }
      },
      immediate: true
    }
  },
  created() {},
  mounted() {
  },
  methods: {
    getOtherQuery(query) {
      return Object.keys(query).reduce((acc, cur) => {
        if (cur !== 'redirect') {
          acc[cur] = query[cur]
        }
        return acc
      }, {})
    },
    login() {
      if (!this.$refs.login_form.validate()) {
        return
      }
      this.loginLoading = true
      const isMail = this.loginForm.account.includes('@')
      this.$store.dispatch('user/login', {
        userName: isMail ? undefined : this.loginForm.account,
        email: isMail ? this.loginForm.account : undefined,
        type: isMail ? 1 : 0,
        userPass: this.loginForm.password,
        deviceType: 0
      }).then((result) => {
        this.loginLoading = false
        this.$router.push({ path: this.redirect || '/', query: this.otherQuery })
      }).catch((err) => {
        console.log(err)
        const data = err.response.data
        this.errMsg = '服务器似乎开小差了，请稍后再试'
        if (data && data.code && (data.code === 5 || data.code === 6)) {
          this.errMsg = '账号或密码错误'
        } else {
          this.$errorMessage()
        }
        this.loginLoading = false
      })
    }
  }
}
